	module.exports = {"data":{"markdownRemark":{"html":"<p>I have been trying to quit programming for about six months. </p>\n<p>I'm down to about 2 times a day, on average, sometimes more. It's a problem. </p>\n<p>Don't get me wrong, programming is one of the best disciplines I ever stuck my nose into. Programming teaches you fundamentally how to <em>constantly improve your craft</em>. But it's exhausting, distracting, and inseparable from the cult of efficiency. It takes you away from humans, mashes your nose into an LCD all night, and leaves you with a delusional sense of total control over your environment. I wish that I could take back every second of my life that I spent learning to build stacks and refactor my own crappy code. </p>\n<p>The problem is that code is so damn useful. It beckons with APIs and gives you entire weekends of mindless flow; hack, hack, hack, make it pretty and fast.</p>\n<p>So anyway, I'm working on it.</p>","frontmatter":{"created_at":"December 03, 2008","path":"/2008/12/03/trying-to-quit","title":"Trying to Quit","tags":["code"]}}},"pathContext":{}}

